<template>
  <div class="sum-page">
    <el-form ref="form" :model="form" label-width="80px" :rules="rules">
      <el-form-item label="产品选择" prop="region">
        <el-select v-model="form.region" placeholder="请选择活动区域">
          <el-option label="无隔板过滤器" value="无隔板过滤器"></el-option>
          <el-option label="有隔板过滤器" value="有隔板过滤器"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="长" prop="long">
        <el-input v-model="form.long"><template slot="append">米(m)</template></el-input>
      </el-form-item>
      <el-form-item label="宽" prop="width">
        <el-input v-model="form.width"><template slot="append">米(m)</template></el-input>
      </el-form-item>
      <el-form-item label="高" prop="height">
        <el-input v-model="form.height"><template slot="append">米(m)</template></el-input>
      </el-form-item>
      <el-form-item style="margin-left: 0">
        <el-button @click="resize">重置</el-button>
        <el-button type="primary" @click="onSubmit">计算</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
name: '',
data() {
  return {
    form: {
      region: '',
      long: '',
      width: '',
      height: ''
    },
    rules: {
      long: [
        {required: true, message: '请输入长度数据', trigger: 'blur'}
      ],
      height: [
        {required: true, message: '请输入高度数据', trigger: 'blur'}
      ],
      width: [
        {required: true, message: '请输入宽度数据', trigger: 'blur'}
      ],
      region: [
        {required: true, message: '请选择活动区域', trigger: 'change'}
      ],
    }
  }
},
  methods: {
    onSubmit()
    {
      this.$refs['form'].validate((valid) => {
        if (valid) {
          this.sum();
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    sum() {
      this.$alert('计算结果', this.form.region, {
        confirmButtonText: '确定',
      });
    },
    resize()
    {
      this.$refs['form'].resetFields()
    }
  }
}
</script>

<style>
  html,body {
    background: #eee;
  }
  .sum-page {
    width: 300px;
    margin: 0 auto;
    /*margin-top: 40%;*/
  }
</style>



